@import url(https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap);
body {
    background: #EBEBEB;;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@media (min-width: 768px) {
.Dashboard{
    width: 100%;
	height: 100%;
}
}
@media (min-width: 768px) {
    .topbar {
        display: flex;
        width: 100%;
        margin-left: 3.5%;
        background: white;
    }
    .topbar-search {
        display: flex;
        flex-direction: row;
        width: 75%;
        max-width: 80%;
    }
    .topbar-search-icon {
        align-items: center;
        display: flex;
        padding-left: 12px;
    }
    .topbar-search-input {
        width: -webkit-fill-available;
        padding: 1.5rem 1.5rem 1.5rem 12px;
        border: none;
        outline: none;
        border-right: 1px solid #EFF4F7;
        ;
    }
    .user-data {
        display: inline-flex;
        flex-direction: row;
        align-items: center;
    }
    .user-img {
        max-width: 2.5rem;
        border-radius: 50%;
        margin: 0 12px 0 0;
    }
    .user-text {
        display: flex;
        flex-direction: column;
       align-items: flex-end;
        margin-left: 12px;
        margin-right: 12px;
        font-family: 'Roboto', sans-serif;
        font-size: 18px;
    }
    .font-size{
        font-size: 12px;
        color: #A6ACBE;
    }
    .icon- {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    .icon-item {
        padding: 1rem;
        align-self: center;
    }
}
@media (min-width: 768px) {
    .side-header {
        position: fixed;
        width: 64px;
        height: 1000px;
        left: 0px;
        top: 0px;
        background: #373B53;
    }
    .logo-rectangle {
        width: 64px;
        height: 64px;
        left: 0px;
        top: 0px;
        background: #00B2FF;
    }
    .logo-word {
        padding: 18px;
    }
    .side-header-menu {
        display: flex;
        flex-direction: column;
        padding: 10px;
        align-items: center;
    }
    .side-header-menu-item {
        margin-top: 12px;
        margin-bottom: 6px;
    }
}
@media (min-width: 768px) {
.cardItem{
    display: flex;
    flex-direction: row;
    margin-left: 65px;
    justify-content: center;
    margin-bottom: 18px;
    margin-top: 12px;
}
}
@media (min-width: 768px) {
    .card{
        display: flex;
        margin-right: 2%;
        width: 400px;
        height: 121px;
        /* border: 1px solid black; */
        box-shadow: 0px 0px 3px #EDEDF6;
        background-color: white;
        border-radius: 10px;
    }
    .card-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-left: 30px;
        background-color: white;
    }
    .card-iteam-2{
        display: flex;
        justify-content: center;
        position: relative;
        top: -45px;
    }
    .card-text{
        margin-left: 20px;
        display: flex;
        flex-direction: column;
    }
}
@media (min-width: 768px) {
    .vercard-top {
        margin: 12px;
        margin-left: 35px;
        display: inline-flex;
        flex-direction: column;
        border: 1px solid #EEF0F7;
        width: 395px;
        border-radius: 10px;
        min-height: 569px;
        max-height: 569px;
        overflow: scroll;
        background-color: white;
    }
    .vercard-top-border {
        display: flex;
        width: 395px;
        border: 1px solid #EEF0F7;
        justify-content: space-around;
        align-items: center;
        padding: 16px;
    }
    .f-small {
        color: #A6ACBE;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
    }
}
@media (min-width: 768px) {
    .ver-item {
        display: flex;
        justify-content: space-around;
        padding: 13px;
    }
    .f-small {
        color: #A6ACBE;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
    }
    .user-img {
        max-width: 2.5rem;
        border-radius: 50%;
        margin: 0 12px 0 0;
    }
    .v-1{
        justify-content:flex-start;
    }
}
@media (min-width: 768px) {
    .ver2-item {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 20px;
    }
    .f-small {
        color: #A6ACBE;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
    }
    .ver-box{
        min-height: 309px;
    }
    .m-1 {
        margin: 12px;
    }
    .m-2 {
        margin: 25px;
    }
}
@media (min-width: 768px) {
    .ver3card-top {
        margin: 12px;
        display: inline-flex;
        flex-direction: column;
        border: 1px solid #EEF0F7;
        width: 830px;
        border-radius: 10px;
        min-height: 435px;
        max-height: 435px;
        overflow: scroll;
        background-color: white;
    }
    .ver3card-top-border {
        display: flex;
        width: 830px;
        border: 1px solid #EEF0F7;
        height: 50px;
        justify-content: space-around;
        align-items: center;
    }
    .f-small {
        color: #A6ACBE;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
    }

}
